"use client";
import { useState, useEffect } from 'react';
import { useAuthState } from 'react-firebase-hooks/auth';
import { auth } from './lib/firebase';
import { DashboardView } from './views/DashboardView';
import { AllListsView } from './views/AllListsView';
import { LandingView } from './views/LandingView';
import { Loader2, LogOut } from 'lucide-react';

function App() {
  const [user, loading, error] = useAuthState(auth);
  const [currentView, setCurrentView] = useState<'dashboard' | 'all-lists'>('dashboard');
  const [timedOut, setTimedOut] = useState(false);
  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  // Safety timeout for auth initialization
  useEffect(() => {
    const timer = setTimeout(() => {
      if (loading) {
        setTimedOut(true);
      }
    }, 5000);
    return () => clearTimeout(timer);
  }, [loading]);

  // Prevent hydration mismatch by not rendering until mounted
  if (!isMounted) {
    return null;
  }

  if (loading && !timedOut) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-surface">
        <div className="text-center">
          <Loader2 className="text-primary animate-spin mx-auto mb-4" size={48} />
          <p className="text-slate-500 animate-pulse">Connecting to Firebase...</p>
        </div>
      </div>
    );
  }

  if (error || (timedOut && !user)) {
    // ... (Existing error UI, slightly customized for new theme)
    return (
      <div className="min-h-screen flex items-center justify-center bg-surface p-6 text-center">
        <div className="max-w-xs">
          <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <LogOut className="text-red-600" size={32} />
          </div>
          <h2 className="text-2xl font-bold text-slate-800 mb-2">Connection Issue</h2>
          <button
            onClick={() => window.location.reload()}
            className="w-full bg-slate-900 text-white py-3 rounded-xl font-bold"
          >
            Retry Connection
          </button>
        </div>
      </div>
    );
  }

  if (!user) {
    return <LandingView onLoginSuccess={() => setCurrentView('dashboard')} />;
  }

  return (
    <div className="min-h-screen bg-surface font-sans text-on-surface selection:bg-primary selection:text-white">
      {currentView === 'dashboard' && (
        <DashboardView
          userName={user.displayName}
          onNavigateToLists={() => setCurrentView('all-lists')}
        />
      )}
      {currentView === 'all-lists' && (
        <AllListsView
          userId={user.uid}
          onBack={() => setCurrentView('dashboard')}
        />
      )}
    </div>
  );
}

export default App;
