import { useState } from 'react';
import { ShoppingCart, Zap, Box, ArrowRight, Loader2 } from 'lucide-react';
import { auth, db } from '../lib/firebase';
import { signInWithPopup, GoogleAuthProvider } from 'firebase/auth';
import { doc, setDoc, serverTimestamp } from 'firebase/firestore';

interface LandingViewProps {
    onLoginSuccess: () => void;
}

export const LandingView = ({ onLoginSuccess }: LandingViewProps) => {
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);

    const handleLogin = async () => {
        setIsLoading(true);
        setError(null);
        try {
            const provider = new GoogleAuthProvider();
            const result = await signInWithPopup(auth, provider);

            if (result.user) {
                await setDoc(doc(db, 'users', result.user.uid), {
                    uid: result.user.uid,
                    email: result.user.email,
                    displayName: result.user.displayName,
                    photoURL: result.user.photoURL,
                    lastLogin: serverTimestamp()
                }, { merge: true });
            }

            onLoginSuccess();
        } catch (err: any) {
            console.error("Login failed:", err);
            if (err.code === 'auth/popup-blocked') {
                setError("Popup blocked! Please allow popups for this site and try again.");
            } else if (err.code === 'auth/configuration-not-found') {
                setError("Firebase Authentication is not configured. Please enable Google Sign-In in the Firebase Console.");
            } else {
                setError(err.message || "Failed to sign in. Please try again.");
            }
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-white">
            <div className="max-w-md mx-auto px-6 pt-20 pb-10 flex flex-col items-center">
                <div className="w-20 h-20 bg-indigo-600 rounded-3xl flex items-center justify-center shadow-xl shadow-indigo-200 mb-8">
                    <ShoppingCart className="text-white" size={40} />
                </div>

                <h1 className="text-4xl font-extrabold text-slate-900 text-center mb-4 tracking-tight">
                    List<span className="text-indigo-600">Pantry</span>
                </h1>
                <p className="text-slate-500 text-center text-lg mb-12">
                    The smartest way to bridge the gap between your shopping list and your kitchen inventory.
                </p>

                {error && (
                    <div className="w-full p-4 mb-6 bg-red-50 border border-red-100 text-red-600 rounded-2xl text-sm text-center">
                        {error}
                    </div>
                )}

                <div className="space-y-6 w-full mb-12">
                    <FeatureCard
                        icon={<Zap className="text-amber-500" />}
                        title="Auto-Replenish"
                        description="Items bought on your shopping list automatically appear in your pantry."
                    />
                    <FeatureCard
                        icon={<Box className="text-emerald-500" />}
                        title="Smart Pantry"
                        description="Track what's in stock and get prompted to re-add items when they run out."
                    />
                </div>

                <button
                    onClick={handleLogin}
                    disabled={isLoading}
                    className="w-full bg-slate-900 text-white py-4 px-8 rounded-2xl font-bold flex items-center justify-center space-x-3 hover:bg-slate-800 transition-all shadow-xl active:scale-[0.98] disabled:opacity-70"
                >
                    {isLoading ? (
                        <Loader2 className="animate-spin" size={20} />
                    ) : (
                        <>
                            <span>Get Started</span>
                            <ArrowRight size={20} />
                        </>
                    )}
                </button>

                <p className="mt-8 text-slate-400 text-sm">
                    Powered by Firebase Authentication
                </p>
            </div>
        </div>
    );
};

const FeatureCard = ({ icon, title, description }: { icon: React.ReactNode, title: string, description: string }) => (
    <div className="flex items-start space-x-4 p-4 rounded-2xl bg-slate-50 border border-slate-100">
        <div className="p-2 bg-white rounded-xl shadow-sm">
            {icon}
        </div>
        <div>
            <h3 className="font-bold text-slate-800">{title}</h3>
            <p className="text-slate-500 text-sm">{description}</p>
        </div>
    </div>
);
